import React, { useEffect, useState } from 'react';
import axios from 'axios';

const DetailView = ({ match }) => {


    const [group, setGorup] = useState({})
    const id = match.params.id


    useEffect(()=>{

        axios({
            method: "GET", 
            url: `http://127.0.0.1:8000/api/group/${id}`
        }).then(res => setGorup(res.data))
    }, [id])


    return(
        <div>
            <span>Group number: {group.number}</span>
            <span>Course number {group.course}</span>
            <span>Faculty number {group.faculty}</span>

        </div>
    )
}




export default DetailView;